.list{
    color: white;
    width: 300px;
    height: 50px;
    background-image: url(../src/home.jpg);
    background-size: cover;
    margin: 20px auto;
    border-radius: 5px;
   

}
.list p{
    padding: 10px;
    position: relative;
}
.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}
