body{
  background-image: url(../src/login.jpg);
     background-size: cover;
}
.App{
  background-image: url(../src/home.jpg);
  background-size: cover;
  min-height: 500px;
  width: 380px;
  margin: 50px auto;
  
  border-radius: 10px;
  box-shadow: 0 10px 25px;
  
}
#todo-form input{
  background-color: rgb(241, 244, 248);
  border: 0;
  width: 200px;
  height: 50px;
  padding:0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: rgb(14, 8, 8);
}
#todo-form input::placeholder{
  color: rgb(12, 10, 10);
}
#todo-form input:focus,#todo-form button:focus{
  outline: none;
}
#todo-form button{
  background-color: yellow;
  width: 80px;
  border-radius: 5px;
  border: 0;
  height: 50px;
  cursor: pointer;
  font-weight: bolder;
  color: blue;



}
